.listItem-container{
    display: flex;
    width:250px;
    margin: 0 1% 1% 0;
    padding: 0.4%;
    border: 1px solid #fff7f7;
}
.listItem-container:hover{
    background-color: #FFEEEB;
}

.listItem-container a{
    display: flex;
    flex-direction: column;
    max-width:100%;
    margin: 0;
    padding: 0;
    text-decoration: none;
}
.listItem-container img{
    border-radius: 5px;
}
.listItem-container a h3{
    color: #330000;
    font-family: "Mandali", Sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    margin: 0; 
}
