
/* MANDALI FONT IMPORT */
@import url('https://fonts.googleapis.com/css2?family=Mandali&display=swap');


/*=== MOBILE VIEW ===*/

/* HEADER  */
.header-main-container{
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  padding: 0;
  /* padding: 0 2%; */
  /* padding: 1% 0 0; */

  
  background-color: #FFEEEB;
}
.header-container-one{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2% 0;
  /* width:94vw; */
  width:100%;
  text-align: center;
  /* border: 1px solid orange; */
}


/* SBTVTELUGU LOGO  */
.sbtvtelugu-logo-container{
  display: flex;
  max-width: 60px;
  margin: 0 0 0 2%;
  /* border: 1px solid blueviolet; */
}
.sbtvtelugu-logo{
  width:100%;
}
.header-add{
  display: none;
}
/* SOCIAL MEDIA BUTTONS  */
.social-media-container{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 45%;
  margin: 0 2% 0 0;
}
.social-media-icons{
  margin: 0;
  padding: 0;
  /* padding: 0 1% 0 0; */
  display: flex;
  justify-content: space-between;
  /* border: 1px dotted green;     */
}
.social-media-icons .social-media-icon{
  display: flex;
  align-items: center;
  width:14px;
  height: 14px;
  padding: 6px;
  margin: 0;
  border-radius: 20px;
  fill:#555555;
  border:1px solid #dddddd;
  transition: all .2s ease-in-out;
}
.social-media-icons .social-media-icon:hover{
  fill:#FF3333;
  border:1px dashed #FF3333;
}

.social-media-container p{
  font-family: "Mandali", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  margin-bottom: 0;
  color:#555555;
  
}


/*=== TABLET VIEW ===*/
@media screen and (min-width:426px){
  .header-add{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 65%;
  }
  .header-add img{
    max-width: 100%;
  }
  .social-media-container {
    width: 21%;
    margin: 0 2% 0 0;
  }
  .social-media-container p {
    font-size: 14px;
  }
}


/*=== LAPTOP VIEW 1024 ===*/
@media screen and (min-width:769px){
  .sbtvtelugu-logo {
    max-width: 80px;
}
  .header-add{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 60%;
    /* width: 70%; */
  }
  .header-add img{
    max-width: 100%;
  }
  .social-media-container {
    width: 16%;
    margin: 0 2% 0 0;
  }
}


/*=== LAPTOP VIEW 1440 ===*/
@media screen and (min-width:1025px){
  .header-container-one {
    align-items: center;
    width: 80%;
  }
  .sbtvtelugu-logo-container {
    margin: 0;
  }
  
  .sbtvtelugu-logo {
    max-width: 70px;
    /* height: 80px; */
}
  .header-add{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 55%;
  }
  .header-add img{
    max-width: 100%;
  }
  .social-media-container {
    width: 15%;
  }
}


/*=== 4K VIEW 2560 ===*/
@media screen and (min-width:1441px){
  .header-main-container {
    padding: 1% 0 0;
  }

  .header-container-one {
    align-items: normal;    
    width: 60%;
    /* width: 60vw; */
    align-items: center;
    padding: 0;
  }
  
  .sbtvtelugu-logo {
    max-width: 80px;
}
  .header-add{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 60%;
  }
  .header-add img{
    max-width: 100%;
  }

  .social-media-container {
    min-width: 20%;
    text-align: center;
}
  .social-media-icons .social-media-icon {
    width: 16px;
    height: 16px;
    padding: 8px;
    margin: 0;
    border-radius: 30px;
}
.social-media-container p {
  font-size: 16px;
}

}



/*=== MENU BAR ===*/

.menu-container{
  background-color: #FF3333;
  margin: 1% 0 0;
  padding: 0.5% 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width:100%;
  /* width: 100vw; */
}
.menu-home-button-container{
  margin: 0;
  padding: 0 1%;
  /* border: 1px solid green; */
}
.menu-home-icon{
  display: flex;
  justify-content: center;
  padding: 0 5% 0 2%;
  fill:#ffffff;
}


.menu-links{
  width: 100%;
  /* width: 100vw; */
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow-x: scroll;
}

/* === to HIDE HORIZONTAL SCROLLBAR ===*/
/* https://www.geeksforgeeks.org/hide-scroll-bar-but-while-still-being-able-to-scroll-using-css/ */

.menu-links::-webkit-scrollbar{
  display: none;
}

.menu-links a{ 
  font-family: "Mandali", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  fill:#ffffff;
  color:#ffffff;
  margin: 0;
  text-decoration: none;
}
.menu-links a:hover{
  text-decoration: underline;
}

.menu-link{
  min-width: 80px;
  text-align: center;
}

/*=== TABLET VIEW ===*/
@media screen and (min-width:426px){
  .menu-links{
    width:100%;
    /* width: 100vw; */
    overflow-x: auto;
  }  
   
} 

/*=== LAPTOP VIEW 1024 ===*/
@media screen and (min-width:769px){
  
  .menu-links{
    width: 90%;
    overflow-x: scroll;
  }
}

/*=== LAPTOP VIEW 1440 ===*/
@media screen and (min-width:1025px){
  .menu-container {
    justify-content: center;
  } 
  .menu-links{
    width: 70vw;
    overflow-x: visible;
  }
  .menu-links a {
    font-size: 16px;
  }
}

/*=== 4K VIEW 2560 ===*/
@media screen and (min-width:1441px){
  .menu-container {
    justify-content: center;
  } 
  .menu-links{
    width: 60vw;
    overflow-x: visible;
  }
  .menu-links a {
    font-size: 16px;
  }

}