.home-main-container{
    display: flex;
    flex-direction: column;
    max-width: 100vw;
    height: 100vh;
    align-items: center;
    margin: 0;
    padding: 0 2%;
    font-family: "Rubik", Sans-serif;
    border: 1px dashed green;
}
.home-main-heading{
    color: #330000;    
    font-size: 20px;
    font-weight: 600;
}

.home-content-container{
    display: flex;
    flex-wrap: wrap;
    width: 63%;
}