.listItemDetails-main-container{
    /* border: 1px solid red; */
    display: flex;
    width: 63%;
    align-self: center;
    padding: 2% 0;
}


.listItemDetails-container{
    display: flex;
    flex-direction: column;
    width:65%;
    /* border: 1px solid yellowgreen; */
}
.listItemDetails-container h5 {
    color: #810000;
    font-family: "Mandali", Sans-serif;
    font-size: 25px;
    font-weight: 400;
    font-style: normal;
    margin: 0 0 1%;
    padding: 0;
}
.listItemDetails-container p {
    color: #300;
    font-family: "Rubik", Sans-serif;
    font-size: 16px;
    font-weight: 400;
    margin: 2% 0;
}
.listItemDetails-container iframe{
    /* width: 772px; */
    width: 99%;
    height: 434px;
    border: none;
}


.listItemDetails-latest-news-container{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    /* align-items: center; */
    text-align: center;
    margin-left: 3%;
    background-color: #FFF7F7;
    /* border: 1px solid yellowgreen; */
}
.listItemDetails-latest-news-container h3{
    color: #ffffff;
    font-family: "Rubik", Sans-serif;
    font-size: 16px;
    font-weight: 400;
    background-color: #dd3737;
    margin: 0;
    padding: 2% 0;
}