
.footer-container{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 2%;
    
    background-color: #FFEEEB;
    border-bottom:15px solid #FF3333;
}


.footer-sub-container-one{
    padding: 2% 0;
    margin-bottom: 10%;
    /* padding: 2% 3%; */
    /* border: 1px solid green; */
}
.sbtvtelugu-footer-logo{
    max-width: 60px;
}
.footer-sub-container-one-content{
    font-family: "Rubik", Sans-serif;
    font-size: 15px;
    font-weight: 400;
    color: #2E0303;
    line-height: 22px;
}
.footer-social-media-icons {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: flex-start;
    /* border: 1px dotted green; */
}
.footer-social-media-icons .footer-social-media-icon {
    display: flex;
    align-items: center;
    width: 14px;
    height: 14px;
    padding: 6px;
    margin: 0 2% 0 0;
    border-radius: 20px;
    fill: #555555;
    border: 1px solid #dddddd;
    transition: all .2s ease-in-out;
}


.footer-sub-container-two{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2%;
    width: 45%;
    background-color: #fff7f7;
    border-radius: 10px;
    margin-bottom: 10%;
}
.footer-sub-container-two img{
    border: 1px solid #2e0303;
}
.footer-sub-container-two p {
    color: #330000;
    font-size: 15px;
    font-weight: 600;
    text-align: center;
    margin: 0;
}


.footer-sub-container-three{
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    align-items: flex-start;
    padding: 2%;
    width: 45%;
}
.footer-sub-container-three a{
    color: #c36;
    font-size: 15px;
    font-weight: 600;
    text-align: center;
    text-decoration: none;
}
.footer-sub-container-three a:hover{
    text-decoration:underline;
}

.footer-sub-container-four{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2% 0;
    width: 100%;
}
.footer-sub-container-four h4{
    color: #330000;
    font-size: 15px;
    font-weight: 600;
    margin: 0;
    padding: 0;
}
.footer-sub-container-four ul{
    margin: 0 0 2% 0;
    padding: 0;
    list-style-type: none;
}
.footer-sub-container-four ul li{
    text-align: center;
    /* text-align: left; */
}
.footer-sub-container-four p{
    display: flex;
    min-width: 100%;
    justify-content: center;
    margin: 0;
}
.footer-sub-container-four p span{
    display: flex;
    fill:#2E0303;
    width: 18px;
    margin-right: 2%;
}

/* TABLET VIEW */
@media screen and (min-width:426px){
    .footer-container {
        padding: 0px 2%;
    }
    .footer-sub-container-one {
        width: 20%;
    }
    .footer-sub-container-one-content {
        font-size: 12px;
    }

    .footer-sub-container-two {
        width: 20%;
    }
    .footer-sub-container-two img {
        width: 50%;
    }
    .footer-sub-container-two p {
        font-size: 12px;
    }

    .footer-sub-container-three {
        width: 20%;
        align-items: flex-start;
    }
    .footer-sub-container-three a {
        font-size: 12px;
    }

    .footer-sub-container-four {
        width: 20%;
        align-items: flex-start;
    }
    .footer-sub-container-four ul {
        font-size: 12px;
    }
    .footer-sub-container-four ul li {
        
        text-align: left;
    }
    .footer-sub-container-four h4 {
        font-size: 14px;
    }
    .footer-sub-container-four p {
        font-size: 12px;
        justify-content: flex-start;
    }
    .footer-sub-container-four p span {
        width: 14px;        
    }
}

/* LAPTOP VIEW 1024*/
@media screen and (min-width:769px){
    .footer-container {
        padding: 0px 2%;
    }
    .footer-sub-container-one {
        width: 23%;        
    }
    .footer-sub-container-two {
        width: 20%;
        margin-bottom: 0; 
    }
    .footer-sub-container-three {
        width: 16%;
        align-items: flex-start;
    }
    .footer-sub-container-four {
        width: 23%;
        align-items: flex-start;
    }
}

