        font-size: 14px;
    }

    .footer-sub-container-three {
        width: 16%;
        align-items: flex-start;
    }
    .footer-sub-container-three a {
        font-size: 14px;
    }
    .footer-sub-container-four ul {
        font-size: 14px;
    }
    .footer-sub-container-four p {
        font-size: 14px;
        justify-content: flex-start;
    }
    .footer-sub-container-four ul li {
        text-align: left;
    }
}

/* 4K VIEW 2560*/
@media screen and (min-width:1441px){
    .footer-container {
        padding: 0px 20%;
    }
    .footer-sub-container-one {
        width: 23%;
        margin-bottom: 0;        
    }
    .footer-sub-container-one-content {
        font-size: 14px;
    }

    .footer-sub-container-two img {
        width: 40%;
    }
    .footer-sub-container-two p {
        font-size: 14px;
    }

    .footer-sub-container-three {
        width: 16%;
        align-items: flex-start;
    }
    .footer-sub-container-three a {
        font-size: 14px;
        margin-bottom: 2%;
    }
    .footer-sub-container-four ul {
        font-size: 14px;
    }
    .footer-sub-container-four p {
        font-size: 14px;
        justify-content: flex-start;
    }
}